export type AuthTokenPayload = {
    sub: string,
    name: string,
    bld: string,
    unit: string,
    ias: string,
    ic: number
}

export type QueryableKV = Partial<{ [k in keyof AuthTokenPayload]: Array<AuthTokenPayload[k]> }>
export type QueryResult = Array<ItemRecord>

export type ItemRecord = {
    type: 'parcel' | string,
    received: number,
    receiver: string,
    lastAt: string,
    lastUpdate: number,
    status: ItemStatus
}

export enum ItemStatus {
    Special = 's',
    Completed = 'c',
    AwaitCollection = 'w',
    Available = 'r',
}

export enum LocalStorageKey {
    JWT = 'authJWT',
    AuthInfo = 'authInfo'
}