import { QueryableKV, QueryResult } from "../types"
import { ENV } from "./env"
import { fetchJsonPost, isOkayJSON } from './util'

export const DatabaseService = {
    async query(queryKV: QueryableKV, token: string) {
        const res = await fetchJsonPost(ENV.api.db.query, queryKV, token)
        return isOkayJSON(res)
            ? { result: await res.json() as QueryResult }
            : {}
    }
}
